.registration-page {
  margin-top: 82px;
}

.registartion-form {
  display: flex;
  flex-direction: column;
  max-width: 362px;

  .input-custom {
    margin-bottom: 20px;
  }

  .select-cont {
    margin-bottom: 20px;
  }

  .confirm {
    margin-top: 10px;
    text-align: center;
    font-family: LR, Arial, sans-serif;
    font-size: 13px;
    color: $black;
    line-height: 100%;
    letter-spacing: 0.1px;
    margin-bottom: 20px;

    a {
      color: $blue;
      text-decoration: underline;
    }
  }
}