.table-container {
  margin-top: 50px;
  position: relative;
  margin-bottom: 70px;
}

.table-container_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;

  .page-title {
    text-align: left;
    margin-bottom: 0;
  }

  .counter-slides {
    font-family: LR, Arial, sans-serif;
    font-size: 14px;
    color: $blue;
    letter-spacing: 1px;
    margin-right: 75px;
  }
}

.legend-list {
  width: 16.666%;
  min-width: 90px;
}

.table-container_information {
  display: flex;
}

.legend-item_desc {
  font-family: LM, Arial, sans-serif;
  font-size: 10px;
  color: $blue;
  line-height: 100%;
  text-transform: uppercase;
}

.legend-item {
  padding: 20px 0;
  position: relative;
  height: 55px;
  display: flex;
  align-items: center;

  &:last-of-type {
    &::before {
      display: none;
    }
  }

  &::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background: #F3F7FB;
    bottom: 0;
    left: -15px;
  }
}

.table-slider {
  width: 100%;
  position: relative;
  max-width: 985px;

  @media screen and (max-width: 1024px) {
    max-width: 865px;
  }

  .slick-prev, .slick-next {
    top: -55px;
  }

  .slick-next {
    right: 33px;
    
    @media screen and (max-width: 1024px) {
      right: 56px;
    }
  }

  .slick-prev {
    right: 124px;
    
    @media screen and (max-width: 1024px) {
      right: 150px;
    }
  }
}

.table-slider_list {
  display: flex!important;
  margin: 0 -5px;
}

.table-slider_item {
  border-radius: 8px;
  border:  1px solid #F3F7FB;
  transition: $anim;
  width: 20%;
  height: 100%;
  padding-bottom: 5px;
  margin: 0 5px;
  max-width: 188px;

  &:hover {
    box-shadow: 0 3px 12px 0 rgba(161, 189, 235, 0.3);

    .table-button {
      color: white;
      background: $blue;
    }
  }

  .inner-item {
    height: 55px;
    display: flex;
    align-items: center;
    padding-left: 25px;
    padding-right: 25px;

    @media screen and (max-width: 1024px) {
       padding-left: 13px;
      padding-right: 13px;
    }
  }

  .inner-desc {
    font-family: LR, Arial, sans-serif;
    font-size: 12px;
    color: $black;
    line-height: 100%;
    letter-spacing: 0.2px;
  }

  .blue-underline {
    color: $blue;
    text-decoration: underline;
  }

  .table-button {
    width: 100%;
    border-radius: 40px;
    color: $blue;
    background: rgba(88, 123, 201, 0.2);
    border: none;
    height: 36px;
    font-family: LB, Arial, sans-serif;
    font-size: 10px;
    letter-spacing: 1px;
    text-transform: uppercase;
    cursor: pointer;
    outline: none;
    transition: $anim;
  }
}

.table-select_wrap {
  position: relative;
  width: 100%;

  &::before {
    position: absolute;
    top: 2px;
    right: 15px;
    color: $blue;
    z-index: 0;
  }

  .table-select_model {
    border: none;
    background: none;
    position: relative;
    z-index: 1;
    appearance: none;
    font-family: LR, Arial, sans-serif;
    font-size: 12px;
    letter-spacing: 0.2px;
    line-height: 100%;
    color: $blue;
    width: 100%;
    outline: none;
  }
}

.table-input {
  font-family: LR, Arial, sans-serif;
  font-size: 12px;
  color: $blue;
  letter-spacing: 0.2px;
  text-decoration: underline;
  border: none;
  width: 100%;
  background: none;
  outline: none;

  &:active, &:focus {
    border-bottom: 1px solid $blue;
  }
}

.table-select_status {
  border-radius: 40px;
  height: 36px;
  font-family: LR, Arial, sans-serif;
  font-size: 12px;
  letter-spacing: 0.1px;
  appearance: none;
  width: 100%;
  border: none;
  padding-left: 20px;
  outline: none;
  position: relative;
  z-index: 1;

  option {
    padding-left: 0;
  }
}

.status_blue_wrap {
  &::before {
    top: 5px;
  }
}

.status_blue {
  color: $blue;
  background: rgba(216, 228, 242, 0.3);
}

.status_red_wrap {
  &::before {
    color: #F44336;
    top: 5px;
  }
}

.status_red {
  color: #F44336;
  background: rgba(244, 67, 54, 0.2);
}

.mdi-custom {
  &::before {
    top: 5px;
  }
}

.status_green_wrap {

  &::before {
    color: #3CC864;
    top: 5px;
  }
}

.status_green {
  color: #3CC864;
  background: rgba(60, 200, 100, 0.2);
}

.date-wrap_table {
  width: 100%;

  .hidden-date {
    max-width: 100%;
    color: $blue;
    background: rgba(161, 189, 235, 0.08);
    border: none;
  }

  svg path {
    fill: $blue;
  }
}

.date-wrap_table_big {
  width: 100%;

  svg {
    path {
      fill: $blue;
      opacity: 0.2;
    }
  }

  .hidden-date_year {
    color: $black;
    border: none;
    background: rgba(216, 228, 242, 0.08);
    width: 100%;
    max-width: 100%;
  }
}